import React, { Fragment, Component } from 'react';

import Slider from "react-slick";
import Swipe from 'react-easy-swipe';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import ReactLoading from 'react-loading';

import api from "../../../util/api";

import RecommendationList from "./index";
import RecomendationCard from './RecomendationCard'
import PremiumPage from '../../../components/PremiumPage'

import eye from "../../../assets/img/recommendation/ic_round-remove-red-eye.svg";
import brain from "../../../assets/img/recommendation/mdi_brain.svg";
import spoonFork from "../../../assets/img/recommendation/si-glyph_spoon-fork.svg";
import sound from "../../../assets/img/recommendation/foundation_sound.svg";
import btnBurger from "../../../assets/img/recommendation/menu.svg";
// import btnBurger from "../../../assets/img/recommendation/btn-burger.svg";
import profile3 from "../../../assets/img/recommendation/profile3.svg";
import empty from "../../../assets/img/recommendation/empty.svg";
import world from "../../../assets/img/recommendation/world.svg";

import style from "../../../components/SuccessPage/success.module.css";

import  introStyle from  './intro.module.css'

// import arrUp from '../../../assets/img/intro/arr_up.svg'
// import arrDown from '../../../assets/img/intro/arr_down.svg'
import arrPointer from '../../../assets/img/intro/arr-pointer.svg'
import circle from '../../../assets/img/intro/circle.svg'
import swipe from '../../../assets/img/intro/swipe2.svg'


const Loading = ({ type, color }) => {
    console.log('Loading Base')
    return (
    <ReactLoading type={type} color={color} height={'20%'} width={'20%'} style={{
        height: '20%',
        width: '20%',
        color : 'white',
        margin: '80% auto'
    }}/>
)};


const Intro = ({setViewed}) => {

    return (
        <div className={introStyle.intro} onClick={setViewed}>
            <div className={introStyle.actions}>
                <div className={introStyle.text}>
                    1. Свайп вверх/вниз для рекомендаций
                </div>
                <img src={swipe} alt="" style={{
                    marginLeft: '35px',
                    marginTop: '20px'
                }}/>
            </div>

            <div className={introStyle.center}>
                    3. Зайди
                <div className={introStyle.circle} >
                    <img src={circle} alt=""/>
                </div>
            </div>

            <div className={introStyle.right}>
                    2. Выбери категорию
                <div className={introStyle.rightPointer}>
                    <img src={arrPointer} alt=""/>
                </div>
            </div>
        </div>
    )
}



class RecomendationBase extends Component{

    constructor(props) {

        super(props)

        this.state = {
            recommendationList: [],
            categoryId: 0,
            currentRecommendation : null,
            categoriesVisible: false,
            introViewed: !!localStorage.getItem('introViewed'),
            showPremium: false,
            categories: [
                {
                    id: 5,
                    text: 'ALL'
                },
                {
                    id: 1,
                    icon: brain
                },
                {
                    id: 2,
                    icon: sound
                },
                {
                    id: 3,
                    icon: spoonFork
                },
                {
                    id: 4,
                    icon: eye
                }
            ],
            coords: null,
            currentSlide: 0
        }
    }

    componentDidMount() {


        if (!localStorage.getItem('navigation')) {
            this.getRecommendation()
        }

        navigator.geolocation.getCurrentPosition(({coords}) => {

            localStorage.setItem('navigation', 'granted')

            this.setState({
                coords
            }, () => {
                this.getRecommendation()
            })
        }, (e) => {

            console.log(e)

        }, {
            enableHighAccuracy: true,
            timeout: 50000,
            maximumAge: 0
        });
    }

    showPremium = (showPremium = true) => {
        this.setState({
            showPremium
        })
    }

    getRecommendation = () => {

        const { categoryId, coords } = this.state

        let params = {}

        if (categoryId) {
            params = {
                recommendationId: categoryId
            }
        }

        if (coords) {
            params = {
                ...params,
                currentLocation: {
                    lat: coords.latitude,
                    lan: coords.longitude
                }
            }
        }

        this.setState({
            pending: true
        })

        console.log('getRecommendation')

        api.get(`recommendation`, {
            params
        }).then(({data}) => {

            this.setState({
                recommendationList: [...data, {
                    subscribeSlide: true
                }],
                pending: false,
                categoryId: data[0].categoryId,
            })
        }).catch(() => {
            this.setState({
                pending: false
            })
        })
    }

    renderEmpty() {
        return (
            <div>
                <div className={style.empty}>
                    <h1 className={style.title}>
                        Мои рекомендации
                        <span style={{marginLeft: '60px'}}>0</span>
                    </h1>
                </div>
                <div className={style.emptypanel}>
                    <img className={style.emptyimg} src={world} alt=""/>
                </div>
            </div>
        )
    }

    render() {

        const { recommendationList, categories, introViewed, showPremium, currentRecommendation, currentSlide, pending, categoriesVisible, categoryId } = this.state

        const settings = {
            dots: true,
            draggable: true,
            className: style.slider,
            responsive: [
              {
                breakpoint: 99999, // These are the settings I want for everything but mobile
                settings: {
                  slidesToShow: 3,
                  centerMode: false,
                  slidesToScroll: 1,
                },
              },
              {
                breakpoint: 800, // Mobile settings reapplied here
                settings: {
                  slidesToShow: 1,
                  slidesToScroll: 1,
                },
              },
            ],
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1,
            centerMode: true,
            centerPadding: '0%',
            infinite: false,
            vertical: true,
            verticalSwiping: true,
            arrows: false,
            currentSlide: 0,
            initialSlide: currentSlide,
            afterChange: (index) => {
                this.setState({
                    currentSlide: index
                })
            }
        };


        return this.state.pending ? <Loading type='bubbles' color='white' /> : (

            <Fragment>

                {!introViewed &&  <Intro setViewed={() => {

                        this.setState({
                            introViewed: true
                        })
                    localStorage.setItem('introViewed', 'viewed')

                }} /> }            

               {showPremium &&  

                <div style={{padding: '50% 30px'}}>
                    <PremiumPage showPremium={() => {
                            this.setState({
                                showPremium: true
                            })
                    }} />
                </div> }

                {currentRecommendation ? <RecomendationCard  {...currentRecommendation}
                                                             categoryId={categoryId}
                                                             setCurrentRecomendation={() => {
                                                                 this.setState({
                                                                     currentRecommendation: null
                                                                 })
                                                             }}/> :

                    <Swipe onSwipeRight={() => {
                        this.props.onSetScreen()
                    }} tolerance = { 100 }>

                        <div>
                            <Slider {...settings} currentSlide={currentSlide}>
                                {recommendationList.length == 0 
                                    ? this.renderEmpty()

                                    : recommendationList.map((recommendation,i) => {

                                        return <RecommendationList {...recommendation}
                                                                   showPremium={this.showPremium}
                                                                   currentSlide={this.state.currentSlide}
                                                                   setCurrentSlide={(currentSlide = 0) => {
                                                                       this.setState({
                                                                           currentSlide
                                                                       })
                                                                   }}
                                                                   setCurrentRecomendation={(data) => {
                                                                       this.setState({
                                                                           currentRecommendation: data
                                                                       })
                                                                   }} key={`${recommendation.id}-${i}`}/>
                                    })}
                            </Slider>
                        </div>
                    </Swipe>
                }


                {this.state.categoriesVisible && <div className={style.catMenu}>
                    {categories.map((cat, i) => {

                        return (
                            <div key={i} className={style.catIcon} onClick={() => {
                                this.setState({
                                    categoryId: cat.id,
                                    categoriesVisible: false
                                }, () => {
                                    this.getRecommendation()
                                })
                            }}>
                                {cat.icon ? <img src={cat.icon} alt=""/> : cat.text}
                            </div>
                        )
                    })}
                </div>}

                {!currentRecommendation &&  <Fragment>
                    <button className={`${style.categoryBtn}  ${categoriesVisible ? `${style.categoryBtnActive}` : ''} `}
                            onClick={() => {
                                this.setState({
                                    categoriesVisible: !categoriesVisible
                                })
                            }}
                    >
                        {categoryId && categoryId !== 5 ?
                            <Fragment>
                                <img src={categories.find(({id}) => {
                                    return id === categoryId
                                }).icon} alt=""/>
                            </Fragment> : <img src={btnBurger} className={style.burger} alt=""/>
                        }
                        <span style={{fontSize: '10px', fontWeight: '900',width: '108%', color: 'white'}}>Категория</span>
                    </button>
                    <button className={`${style.profileBtn} `} onClick={() => {
                            this.props.onSetScreen()}}
                    >
                        <img src={profile3} alt="" style={{margin: '5px 25%', height: '28px'}}/>
                        <span style={{fontSize: '10px', fontWeight: '900', width: '93%', color: 'white'}}>Профиль</span>
                    </button>
                </Fragment>}
            </Fragment>
        );
    }
}

export default RecomendationBase