import React, { useRef } from 'react';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import style from './onBoarding.module.css'

import arrow from '../../assets/img/chevron-right.svg'
import logoWhite from '../../assets/img/logo-white.svg'
import step1 from '../../assets/img/step1.png'
import step2 from '../../assets/img/step2.png'
import step3 from '../../assets/img/step3.png'
import step4 from '../../assets/img/step4.png'

const config = [
    {
        title: 'Укажи свои интересы!',
        text: 'Автоматически анализирует профиль инстаграма и определяет персональные интересы',
        imageUrl: step1
    },
    {
        title: 'Выбери тип места!',
        text: 'Поесть, посмотреть, послушать или подумать...',
        imageUrl: step2
    },
    {
        title: 'Буде первым кто узнает про новое место!',
        text: 'Лучшие места рядом',
        imageUrl: step3
    },
    {
        title: 'Открой заново лучшие места в своем городе!',
        text: 'Начни сейчас',
        imageUrl: step4
    }
]

const OnBoarding = (({onConfirm = () => {}}) => {

    const settings = {
        dots: false,
        infinite: false,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: false
    }

    const slider = useRef(null);

    return (
        <Slider {...settings} ref={slider}>
            {config.map((slide, i) => {
                return (
                    <div className={style.slide} key={i}>
                         <div className={style.image}>
                             <img src={slide.imageUrl} alt=""/>
                         </div>
                        <div className={style.body}>
                            <div className={style.title}>
                                {slide.title}
                            </div>
                            <div className={style.description}>
                                {slide.text}
                            </div>
                            <div className={style.dots}>
                                {config.map((dot, dotIndex) => {
                                    return (
                                        <button className={`${style.dot} ${i === dotIndex ? `${style.dotActive}` : ''}`}
                                            onClick={() => {

                                                if (i === dotIndex) {
                                                    return false
                                                }
                                                slider.current.slickGoTo(dotIndex)
                                            }}
                                        />
                                    )
                                })}
                            </div>
                        </div>
                    </div>
                )
            })}
            <div>
                <div className={style.confirmScreen}>
                    <img src={logoWhite} className={style.logo} alt=""/>
                    <div className={style.confirmSummary}>
                        Now we will get the tags you want from instagram
                    </div>
                    <div className={style.confirmBlock}>
                        <div className={style.confirmText}>
                            Просканируй  свой аккаунт!
                        </div>
                        <button className={style.confirmBtn} onClick={() => {
                            onConfirm()
                        }}>
                            <img src={arrow} alt="" style={{width: '20px'}} />
                        </button>
                    </div>
                </div>
            </div>

        </Slider>
    )
})

export default OnBoarding