import React, { Component } from 'react';

import CircleBtn from "../../components/CircleBtn";
import OnBoarding from './OnBoarding'

import api from '../../util/api'

import style from './auth.module.css'
import style2 from "../../components/SuccessPage/success.module.css";

import logo from '../../assets/img/auth/auth_logo.svg'
import button from '../../assets/img/circle.png'
import profile from "../../assets/img/recommendation/profile.svg";


class Auth extends Component{

    constructor(props) {
        super(props);

        this.state = {
            pending: false,
            optionsVisible: false,
            landed: false,
            categories: [
                {
                    id: 1,
                    text: 'Try'
                },
            ],
        }
    }

    openLoginWindow = () => {
        const w = 500
        const h = 500

        const left = (window.width/2)-(w/2);
        const top = (window.height/2)-(h/2);

        const newWin = window.open(`about:blank`, "auth", `toolbar=no, location=no, directories=no, status=no, menubar=no, scrollbars=no, resizable=no, copyhistory=no, width=${w}, height=${h}, top=${top}, left=${left}`);

        const interval = setInterval(() => {

           try {

               const location = new URL(newWin.location)

               if (location.searchParams.get('code')) {
                   clearInterval(interval)
                   newWin.close()
                   this.onLogin(location.searchParams.get('code'))
               }
           } catch (e) {
               console.log(e)
           }

       }, 1000)

        newWin.location.href = 'https://www.instagram.com/oauth/authorize?client_id=476859443194257&redirect_uri=https://secure.stayandsmile.com/callback&scope=user_profile&response_type=code'
    }

    openLoginWindow2 = () => {
        const interval = setInterval(() => {
           try {
               clearInterval(interval)
               this.onLogin('test')
           } catch (e) {
               console.log(e)
           }
       }, 1000)
    }

    onLogin = (code) => {
        // var code = 'AQB1pc-2EbP-gMXMEhH_1Hu5r0RgZYEaTuT7Ztinxccx35Xdr62d-8W2fiUhvqCUGvxj9jzcrJnO7FsgPETD6gyavf4_5Fw7pCFaqyNwoT_VMphKR_2FKQkefxaGPYwxajhfVqid9K-UeYby43YkrCAUywGmZ9DxR-QX0VSrVsGw6nWr29eC308g2MVtDdjhXAd9G1UOihfD4LGba6L0Q9R-GVWGFCTmvmTthj9YH-uuTA#_';

        this.setState({
            pending: true
        })

        api.post('login', {
            code
        }).then((res) => {

            this.setState({
                pending: false
            })

            if (res.data.accessToken) {
                this.props.setToken(res.data.accessToken)
                // window.location.reload();
            }
        }).catch(() => {
            this.setState({
                pending: false
            })

        })
    }

    componentDidMount() {
        this.onLogin()
    }

    render() {

        const { pending, categories, optionsVisible,landed } = this.state

        if (!landed) {
            return <OnBoarding
                onConfirm={() => {
                    this.setState({
                        landed: true
                    })
                }}/>
        }

        return (
            <div className={style.body}>
                <div className="content-fix">
                    <div className={style.logo}>
                        <img src={logo} alt="" className=""/>
                    </div>
                    <div className={style.footer}>

                    {this.state.optionsVisible && <div className={style2.catMenu}>
                          {categories.map((cat, i) => {

                              return (
                                  <div key={i} className={style2.catIcon} onClick={() => {
                                      this.setState({
                                          optionsVisible: false
                                      }, () => {
                                          this.openLoginWindow2()
                                      })
                                  }}>
                                      {cat.icon ? <img src={cat.icon} alt=""/> : cat.text}
                                  </div>
                              )
                          })}
                      </div>}

                        <button style={{padding:'16px 0 0 14px'}} className={`${style2.authBtn}  ${optionsVisible ? `${style2.categoryBtnActive}` : ''} `}
                            onClick={() => {
                                this.setState({
                                    optionsVisible: !optionsVisible
                                    })
                                }}
                        >
                            {
                              'more'
                            }
                          </button>
                        <CircleBtn onClick={this.openLoginWindow} disabled={pending} style={{
                              color: 'var(--white)', 
                              backgroundImage: `url(${button})`, 
                              backgroundColor: 'transparent', 
                              borderRadius: '100px' 
                        }}>
                            GO
                        </CircleBtn>
                        <p className={style.text}>
                            Sign up with Facebook to access your Instagram
                        </p>
                       
                    </div>
                </div>
            </div>
        )
    }
}

export default Auth